.explore {
  display: grid;
  grid-template-columns: repeat(12, 1fr);
  grid-template-rows: auto auto;
  row-gap: 48px;

  &__content {
    display: grid;
    justify-items: center;
    grid-column: 1 / -1;

    @include onDesktop {
      grid-column: 1 / -1;
    }

    grid-template-columns: repeat(12, 1fr);
    grid-template-rows: auto auto auto;

    @include onDesktop {
      grid-template-rows: auto;
    }

    gap: 48px 24px;
  }

  &__title {
    grid-row: 1;
    grid-column: 1 / -1;
    text-align: center;
  }

  &__part-bottom {
    grid-column: 1 / -1;
    display: flex;
    flex-direction: column;
    align-content: center;
  }

  &__part-bottom-1 {
    grid-column: 1 / -1;
    display: flex;
    flex-direction: column;
    align-content: center;

    @include onDesktop {
      margin-top: -26px;
    }
  }


  &__part-title {
    color: $accent-main;
    line-height: 140%;
  }

  &__description {
    font-family: Roboto, sans-serif;
    font-size: 16px;
    font-weight: 400;
    line-height: 27px;
    max-width: 100%;
  }

  &__part-1 {
    display: grid;
    row-gap: 24px;
    grid-column: 3 / -3;
    grid-row: 1;

    text-align: center;

    @include onDesktop {
      grid-column: 1 / 5;
      row-gap: 32px;
      grid-row: 1;
    }
  }

  &__part-2 {
    display: grid;
    row-gap: 24px;
    grid-column: 3 / -3;
    grid-row: 2;

    text-align: center;

    @include onDesktop {
      grid-column: 5 / 9;
      row-gap: 32px;
      grid-row: 1;
    }
  }

  &__part-3 {
    display: grid;
    row-gap: 24px;
    grid-column: 3 / -3;
    grid-row: 3;

    text-align: center;

    @include onDesktop {
      grid-column: 9 / -1;
      row-gap: 32px;
      grid-row: 1;
    }
  }

  &__image * {
    display: block;

    @include hoverScaleTransition-img;
  }
}
